















import { apiCall } from "@/api";
import SearchService from "@/api/service/search.service";
import { messages } from "@/constants";
import Vue from "vue";
export default Vue.extend({
  data: () => ({
    search: "",
    searchService: new SearchService(apiCall),
    searchResult: {},
  }),
  methods: {
    goSearch() {
      if (this.search.trim() != "") {
        (this.$root.$refs.loading as any).open();
        this.searchService
          .search(this.search)
          .then((res) => {
            if (res.status) {
              this.searchResult = res.result;
            }
            this.showMessage(res.title);
          })
          .catch((e) => {
            this.showMessage(messages.netWorkError(e.message).title);
          });
      }
    },
    showMessage(text: string) {
      (this.$root.$refs.loading as any).close();
      (this.$root.$refs.snackbar as any).open(text);
    },
  },
});
